import React from "react";
import styled from "styled-components";
import Section from "components/Section";
import Title from "components/Title";
import IconLg from "images/logo_lg.png";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";

const imgTemp = 'data:image/png;base64,';

const StoryContainer = styled.div`
  position: relative;
  overflow: hidden;
`;

const StoryArea = styled.div`
  display: flex;
  flex-direction: column;
  .swiper-slide {
    width: 90%;
  }
`;

const Link = styled.a`
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 600px) {
    display: flex;
    flex-direction: row;
  }
`;

const StoryImg = styled.img`
  width: 100%;
  height: auto; 
`;

const StoryText = styled.div`
  position: relative;
  @media screen and (min-width: 600px) {
    display: flex;
    flex: 1;
    flex-direction: column;
  }
`;

const StoryTitle = styled.div`
  overflow: hidden;
  display: -webkit-box;
  width: 100%;
  margin-top: 17px;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow-wrap: break-word;
  text-overflow: ellipsis;
  ${({ theme }) => theme.fontSet.Type_A08};
  font-weight: ${({ theme }) => theme.fontWeight.Bold};
  @media screen and (min-width: 600px) {
    margin-top: 0;
  }
`;

const StoryDesc = styled.div`
  overflow: hidden;
  display: -webkit-box;
  width: 100%;
  margin-top: 5px;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow-wrap: break-word;
  text-overflow: ellipsis;
  ${({ theme }) => theme.fontSet.Type_A06};
  font-weight: ${({ theme }) => theme.fontWeight.Regular};
  @media screen and (min-width: 600px) {
    white-space: unset;
  }
`;

const StoryImgDiv = styled.div`
  @media screen and (min-width: 600px) {
    display: flex;
    flex: 1;
    margin-right: 10px;
  }
`;

const StoryThumnail = styled.div`
  text-decoration: none;
  overflow: hidden;
  border-radius: 10px;
  font-size: 0;
`;

const ListItems = styled.div`
  overflow: hidden;
  box-sizing: border-box;
`;

const Story = ({ storyData }) => {
  return(
    <Section>
      <StoryContainer>
        <Title className={storyData ? "" : "title-box"} link={`https://m.naver.com`} iconSrc={storyData ? IconLg : ""} iconText={`LG 로고`}>
          {storyData ? "에어로타워 스토리" : "xxxxxxxxxx"}
        </Title>
        <StoryArea>
          <Swiper spaceBetween={10} slidesPerView={"auto"} loop={true}>
            {!storyData && (
              <SwiperSlide>
                <ListItems key={0}>
                  <Link  href="">
                    <StoryImgDiv>
                      <StoryThumnail className="img-box">
                        <StoryImg src={imgTemp} alt=""/>
                      </StoryThumnail>
                    </StoryImgDiv>
                    <StoryText>
                      <StoryTitle className="text-box">xxxxxxxxxxxxxx</StoryTitle>
                      <StoryDesc className="text-box">xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx</StoryDesc>
                    </StoryText>
                  </Link>
                </ListItems>
              </SwiperSlide>
            )}
            {storyData && storyData.map(({ contentId, contentUrl, thumbnail, contentTitle, contentDescription }) => (
              <SwiperSlide>
                <ListItems key={`story_${contentId}`}>
                  <Link href={contentUrl}>
                    <StoryImgDiv>
                      <StoryThumnail>
                        <StoryImg src={thumbnail} alt={'리뷰 이미지'}/>
                      </StoryThumnail>
                    </StoryImgDiv>
                    <StoryText>
                      <StoryTitle>{contentTitle}</StoryTitle>
                      <StoryDesc>{contentDescription}</StoryDesc>
                    </StoryText>
                  </Link>
                </ListItems>
              </SwiperSlide>
            ))}
          </Swiper>
        </StoryArea>
      </StoryContainer>
    </Section>
  );
}

export default Story;